    //     city = cookies.city
    //   }
    // } else {
    //   city = utils.getCity()
    // }
    try {
      const [postsPage] = await Promise.all([
        $axios.get('/api/tiaoji/posts', {
          params: {
            page: query.p || 1
          }
        })
        // $axios.get('/api/user/score/rank'),
        // $axios.get('/api/link/toplinks'),
        // $axios.get('/api/tweet/newest')
      ])
      return { postsPage }
    } catch (e) {
      console.error(e)
    }
  },
  data() {
    return {
      searchTerms: '',
      searchAction: false,
      majorId: 0,
      provinceId: 0,
      pageAction: '/api/tiaoji/posts',
      city_list: []
    }
  },
  mounted() {
    this.city_list = utils.getCityList()
    this.pageAction = '/api/tiaoji/posts'

    const tiaojiMajor = Cookies.get('tiaoji_major')
    if (tiaojiMajor) {
      const selectMajor = JSON.parse(tiaojiMajor)
      this.majorId = selectMajor.id
    }

    const tiaojiProvince = Cookies.get('tiaoji_province')
    if (tiaojiProvince) {
      const selectProvince = JSON.parse(tiaojiProvince)
      this.provinceId = selectProvince.provinceId
    }

    this.fetchPosts()
  },
  methods: {
    onPageChanged(pageAction, page) {
      this.changePageTopics(
        pageAction,
        { page, province_id: this.provinceId, major_id: this.majorId },
        null
      )

      setTimeout(() => {
        window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
      }, 300)
    },
    onMajorChanged(majorId) {
      this.majorId = majorId
      this.fetchPosts()
    },
    onProvinceChanged(provinceId) {
      this.provinceId = provinceId
      this.fetchPosts()
    },
    fetchPosts() {
      this.changePageTopics(
        this.pageAction,
        {
          province_id: this.provinceId,
          major_id: this.majorId
        },
        () => {
          this.refreshPagination()
        }
      )
    },
    onCollapseBack(state) {
      // console.log('onCollapse:' + state)
    },
    jumpCityWechatUrl() {
      window.location.href = this.jumpCityUrl
    },
    jumpUrl(href) {
      if (href) {
        window.open(href)
      } else {
      }
    },
    twitterCreated(data) {
      if (this.postsPage) {
        if (this.postsPage.results) {
          this.postsPage.results.unshift(data)
        } else {
          this.postsPage.results = [data]
        }
      }
    },
    changePageTopics(url, params, callback) {
      this.pageAction = url
      this.$axios
        .get(url, { params })
        .then((res) => {
          this.postsPage.results = res.results
          this.postsPage.page = res.page
          if (callback) {
            callback()
          }
        })
        .catch((e) => {
          console.log('tagSearchTopics error:' + JSON.stringify(e))
        })
    },
    refreshPagination() {
      const state = this.searchAction
      this.searchAction = !this.searchAction
      this.$nextTick(() => {
        this.searchAction = state
      })
    },
    refreshRentRecommend() {
      const state = this.showRentRecommentDiv
      this.showRentRecommentDiv = !this.showRentRecommentDiv
      this.$nextTick(() => {
        this.showRentRecommentDiv = state
      })
    },
    geoSearchTopics(rentItem) {
      // this.$axios
      //   .get('/api/topic/tag/topiclist', {
      //     params: {
      //       page: 1,
      //       tagName
      //     }
      //   })
      //   .then((res) => {
      //     this.topicsPage.results = res.results
      //     this.topicsPage.page = res.page
      //   })
      //   .catch((e) => {
      //     console.log('tagSearchTopics error:' + JSON.stringify(e))
      //   })
    },
    // async search() {
    //   try {
    //     await this.$axios.post('/api/user/edit/' + this.user.id, {
    //       nickname: this.form.nickname,
    //       avatar: this.form.avatar,
    //       homePage: this.form.homePage,
    //       description: this.form.description
    //     })
    //     await this.reload()
    //     this.$toast.success('资料修改成功')
    //   } catch (e) {
    //     console.error(e)
    //     this.$toast.error('资料修改失败：' + (e.message || e))
    //   }
    // },
    jumpCreateRent() {
      utils.linkTo('/rent/create')
    },
    search() {
      const search = this.searchTerms.trim()
      if (utils.isEmpty(search)) {
        this.$toast.error('请输入地点')
        return
      }

      const that = this

      GeoUtil.requestAmapGeo(this.$axios, search, (geoObject) => {
        const result = GeoUtil.getLatlonFromGeoRequest(geoObject)
        // console.log('requestAmapGeo:' + JSON.stringify(result))
        that.showRentRecommentDiv = true
        that.selectStation = that.getSelectStationObject(result.geohash)
        that.refreshRentRecommend()
      })

      this.$axios
        .get('/api/topic/search', {
          params: {
            terms: search,
            city: utils.getCity()
          }
        })
        .then((res) => {
          this.postsPage.results = res
          this.searchAction = true
        })
        .catch((e) => {
          console.log('search error:' + JSON.stringify(e))
        })
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区—出租/转租/合租找室友'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
